 @import '../../../base';

$width-medium: 200px;
$height-medium: 72px;

$width-small: 97px;
$height-small: 36px;

$border-radius-medium: 36px;
$border-radius-small: 21px;

$before-grey-medium: linear-gradient(108deg, $color-line, $color-line);
$after-grey-medium: linear-gradient(108deg, $color-caption, $color-caption);
$active-grey-medium: linear-gradient(108deg, $color-caption, $color-caption);

$before-color-medium: linear-gradient(108deg, $color-hover, $color-body);
$after-color-medium: linear-gradient(108deg, $color-hover, $color-hover);
$active-color-medium: linear-gradient(108deg, $color-line, $color-line);

$before-flat-medium-light-green: linear-gradient(108deg, $color-hover-alt3, $color-hover-alt3);
$before-flat-medium: linear-gradient(108deg, white, white);
$after-flat-medium: linear-gradient(108deg, $color-hover, $color-hover);
$active-flat-medium: linear-gradient(108deg, $color-line, $color-line);

$before-flat-small:  linear-gradient(to bottom, $color-hover-alt2, $color-hover);
$after-flat-small: linear-gradient(to bottom, $color-hover-alt, $color-hover-alt);
$active-flat-small: linear-gradient(to bottom, $color-line, $color-line);

$before-flat-small-dark: linear-gradient(108deg, $color-title, $color-title);
$after-flat-small-dark: linear-gradient(108deg, $color-body, $color-body);
$active-flat-small-dark: linear-gradient(108deg, $color-caption, $color-caption);

$disabled-color: linear-gradient(108deg, $color-line, $color-line);

$shadow-medium: 0 25px 30px 0 rgba(70, 115, 122, 0.15);
$shadow-small:  0 6px 8px 0 rgba(0, 0, 0, 0.1);
$shadow-green: 0px 5px 6px rgba(72, 255, 211, 0.4);
$shadow-none:  0;

$outline-before: solid 1px $color-title;
$outline-after: solid 1px $color-hover;
$outline-before-edit: solid 1px $color-input-form-landing-desc2;

@mixin spinner-animation-template(
  $height,
  $color
) {
  .spinner-animation {
    $spinner-height: $height * 0.5;
    width: $spinner-height !important;
    height: $spinner-height !important;
    left: calc(50% - #{$spinner-height} * 0.5) !important;
    top: calc(50% - #{$spinner-height}  * 0.5) !important;

    .dot1,
    .dot2 {
      width: $spinner-height * 0.5 !important;
      height: $spinner-height * 0.5 !important;
      background-color: $color !important;
    }
  }
}

@mixin button-custom-template(
  $width, 
  $height, 
  $radius, 
  $before-color, 
  $after-color, 
  $active-color,
  $before-text,
  $hover-text,
  $font-size,
  $font-weight,
  $shadow
) {
  width: $width;
  max-width: 100%;
  min-height: $height;

  border-radius: $radius;
  background-image: $before-color;
  box-shadow: $shadow;
  position: relative;
  border: none;
  z-index: 1;
  padding: 0 20px;

  &:before {
    border-radius: $radius;
    background-image: $after-color;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0; left: 0;
    opacity: 0;
    z-index: -100;
    transition: opacity 0.2s;
  }

  &:hover {
    text-decoration: none;
     &:before {
       opacity: 1; 
     }

     p {
       color: $hover-text;
     }
  }

  &:active {
    &:before {
      opacity: 0; 
    }
    background-image: $active-color;
  }

  &:focus {
    outline: none;
  }

  p {
    margin: auto;
    font-size: $font-size;
    font-weight: $font-weight;
    color: $before-text;
  }

  @include spinner-animation-template($height, white);
}

@mixin button-outline-template(
  $width,
  $height,
  $radius,
  $border-before,
  $border-after,
  $text-size,
  $text-weight,
  $text-color-before,
  $text-color-after,
  $default-color,
  $active-color
) {
  width: $width;
  max-width: 100%;
  min-height: $height;

  border-radius: $radius;
  border: $border-before;
  transition: border 0.2s;
  padding: 0 20px;
  background-color: $default-color;
  position: relative;

  &:focus {
    outline: none;
  }

  &:hover {
    text-decoration: none;
    border: $border-after;

    p {
      color: $text-color-after;
    }
  }

  &:active {
    background-color: $active-color;
  }

  p {
    font-size: $text-size;
    font-weight: $text-weight;
    color: $text-color-before;
    transition: color 0.2s;
    margin-bottom: 0;
  }

  @include spinner-animation-template($height, $color-title);
}

.disabled {
  background-image: $disabled-color;
  border: none !important;

  &:hover {
    text-decoration: none;
    &:before {
      opacity: 0 !important; 
    }
  }

  p {
    color: white !important;
  }
}

